import React, { Component, useMemo } from 'react'
import { Button, Image, Table, Form, OverlayTrigger, Tooltip, Dropdown } from 'react-bootstrap';
import Author from '../../../CommonComponents/Author/Author';
import { Link } from 'react-router-dom';
import { connect, useDispatch } from 'react-redux'
import { ACTION_UPDATE_SOCIAL_ACCOUNT, GET_FEEDS, uploadFeedProfileImage } from '../../../../../../actions/manageFeedsAction'
import { formatTemplateText, GET_NETWORK_NAME, HTMLParser, showAddAccounntAlert, switchWarning } from '../../../../../../utils'
import { GET_NETWORK_ICON } from '../../../../../../utils';
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { checkPermission } from '../../../../../../actions/collaboratorActions';
import Uploader from '../../../CommonComponents/Uploader';
import { COMPANY_NAME } from '../../../../../../actions/api';

const isProfileImgEditAble = (network, filterId)=>{
    if(network == 18 || network == 2){
        return [23, 34, 66, 1].includes(filterId);
    }else if(network == 10){
        return [17, 1, 86].includes(filterId);
    }else if(network == 3){
        return [8, 1, 67, 65, 66].includes(filterId);
    }else if(network == 7){
        return [1, 75].includes(filterId);
    }else if(network == 28){
        return [2, 71].includes(filterId);
    }else if(network == 12){
        return [13].includes(filterId);
    }else if(network == 5){
        return [1, 71].includes(filterId);
    }else if(network == 1 || network == 8 || network == 6 || network == 11){
        return [1].includes(filterId);
    }
    return false;
}

class FeedsList extends Component {
    constructor(props) {
        super(props)

        this.state = {
            moderation: this.props.feed.moderation,
            status: this.props.feed.status,
            connectedAccount: this.getConnectAccount(),
        }
    }

    getConnectAccount = () => {
        const { accountList, connectedAccount } = this.props.feed;
        const connectedAcc = accountList.length > 0 ? accountList.filter((account) => account.id === connectedAccount) : [];
        return connectedAcc.length > 0 ? connectedAcc[0] : accountList.length > 0 ? accountList[0] : {}
    }

    componentDidMount() {
        const { feed } = this.props
        this.setState({ status: feed.status, moderation: feed.moderation })
    }

    onModerationSwitch = (status) => {
        const { feed, changeStatus } = this.props
        changeStatus(feed.id, Number(status), 'moderation')
        this.setState({ moderation: Number(status) })
    }

    onFeedStatusSwitch = (status) => {
        const { feed, changeStatus } = this.props
        changeStatus(feed.id, Number(status), 'status')
        this.setState({ status: Number(status) })
    }

    connectAccount = (obj, status) => {
        const item = {
            id: obj.networkId,
            name: (obj.networkName).toLowerCase()
        }
        localStorage.setItem("feedId", obj.id);
        if (status) localStorage.setItem("refresh", true);
        localStorage.setItem("connectionType", 'manageFeed');
        showAddAccounntAlert(item, status, true)
    }

    componentDidUpdate(prevProps) {
        if (prevProps.accountChanged != this.props.accountChanged) {
            this.setState({ connectedAccount: this.getConnectAccount() })
        }
    }

    render() {
        const { feed, onFeedDelete, changeAccount } = this.props
        const { status, moderation, connectedAccount } = this.state
        const { filterId, networkId, accountList, isBtntext } = feed;
        const wallId = localStorage.getItem("wallId");
        const uploadPath = `media/${localStorage.getItem("ownerId")}/${wallId}/${feed.id}`;
        const isEdit = isProfileImgEditAble(networkId, filterId);
        const appContent = window?.APP_CONTENT ?? {};
        
        return (
            <tr>
                <td>
                    <div className="d-flex align-items-center">
                        <span className="h-34px w-34px me-2 d-flex flex-shrink-0">
                            <Image className="h-100" src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(feed.networkId)}.svg`)} height={43} width={43} title={GET_NETWORK_NAME(feed.networkId)} alt={GET_NETWORK_NAME(feed.networkId)} fluid/>
                        </span>
                        <div style={{ minWidth: 170 }}>
                            <p className="mb-0 d-flex">
                                <span className="feed_name fw-semibold">{HTMLParser(feed.name)}{" "}</span>
                                {feed.socialLink ?
                                    <OverlayTrigger placement="top" overlay={<Tooltip>{feed.tooltip}</Tooltip>}>
                                        <Link className="ms-2 text-muted" to={feed.networkId !== 8 ? feed.socialLink : `https://vimeo.com/user${feed.accountId}`} target="_blank" title='support'>
                                            <FontAwesomeIcon icon={'arrow-up-right-from-square'} />
                                        </Link>
                                    </OverlayTrigger>
                                    : null}
                            </p>
                            <small className="mb-0 text-gray-700">{feed.networkName}</small>
                        </div>
                    </div>
                </td>
                <td><AccountManager feed={feed} connectionType={"manageFeed"} /></td>
                <td>
                    <p className="fs-8 fw-semibold mb-0" data-id="post" style={{minWidth: 120}}>{feed?.postCount || 0}</p>
                    <span className="fs-8 text-gray-700">{formatTemplateText(appContent?.manage_feed?.feed_post_count ?? "", { feed_post_count: feed?.postCount || 0, post_label: feed?.postCount > 1 ? appContent?.manage_feed?.posts : appContent?.manage_feed?.post })}</span>
                </td>
                <td>
                    <Form.Check type="switch" checked={moderation} data-id="moderation" onChange={() => checkPermission('networkManager', 'manageFeed', true) ? moderation ? this.onModerationSwitch(!moderation) : switchWarning(appContent?.manage_feed?.enable_moderation ?? "", this.onModerationSwitch, !moderation) : {}} />
                </td>
                <td>
                    <Form.Check type="switch" checked={status} data-id="status" onChange={() => checkPermission('networkManager', 'manageFeed', true) ? !status ? this.onFeedStatusSwitch(!status) : switchWarning(appContent?.manage_feed?.disable_status ?? "", this.onFeedStatusSwitch, !status) : {}} />
                </td>
                <td className="pr-0">
                    <div className='d-flex justify-content-end'>
                        <div className="d-flex align-items-center justify-content-end me-2">
                            {(checkPermission('networkManager', 'manageFeed', true) && isEdit) ?
                                <Uploader uploadPath={uploadPath} uploadAgents={[]} onImgUpload={(path) => uploadFeedProfileImage(path, feed.id)}>
                                    <OverlayTrigger placement="top" overlay={<Tooltip>{appContent?.common?.upload ?? ""}</Tooltip>}>
                                        <Button variant='icon' size='sm' data-id="edit" className="btn-active-primary" aria-label="edit">
                                            <FontAwesomeIcon type="solid" icon={'edit'} />
                                        </Button>
                                    </OverlayTrigger>
                                </Uploader> : null}
                        </div>
                        <div className="d-flex align-items-center justify-content-end" onClick={() => checkPermission('networkManager', 'manageFeed', true) ? onFeedDelete(feed.id, feed.networkId) : {}}>
                            <OverlayTrigger placement="top" overlay={<Tooltip>{appContent?.common?.delete ?? ""}</Tooltip>}>
                                <Button variant='icon' size='sm' data-id="delete" className="btn-active-primary" aria-label="delete">
                                    <FontAwesomeIcon type="solid" icon={'trash-can'} />
                                </Button>
                            </OverlayTrigger>
                        </div>
                    </div>
                </td>
            </tr>
        )
    }
}

const mapDispatchToProps = dispatch => {
    return {
        GET_FEEDS: () => dispatch(GET_FEEDS()),
    }
}

export default connect(null, mapDispatchToProps)(FeedsList);

export const AccountManager = ({ feed, connectionType }) => {
    const { filterId, networkId, accountList, isBtntext, connectedAccount } = feed;
    const dispatch = useDispatch();
    const isAccessible = useMemo(() => checkPermission('networkManager', 'manageFeed', true), []);
    const appContent = useMemo(() => window?.APP_CONTENT?.manage_feed ?? {}, []);

    const selectedAccount = useMemo(() => {
        const connectedAcc = connectedAccount ? accountList?.filter((account) => account.id === connectedAccount) ?? [] : [];
        return connectedAcc?.[0] ?? {};
    }, [accountList, connectedAccount]);

    const connectAccount = (obj, status) => {
        if (isAccessible) {
            const item = {
                id: obj.networkId,
                name: (obj?.networkName).toLowerCase() ?? ""
            }
            localStorage.setItem("feedId", obj.id);
            if (status) localStorage.setItem("refresh", true);
            localStorage.setItem("connectionType", connectionType);
            showAddAccounntAlert(item, status, true)
        }
    }

    return <div className="d-flex align-items-center">
        {(([1, 2, 3, 4, 5, 8, 65, 67, 26, 23, 86, 89, 90].includes(filterId) && networkId != 2) || (filterId == 25 && networkId == 2)) && accountList?.length > 0 ?
            <Dropdown style={{ minWidth: 220 }}>
                <Dropdown.Toggle variant="secondary" size={'sm'} className="d-flex align-items-center w-100 justify-content-between p-2">
                    <div className="d-flex align-items-center flex-grow-1">
                        <Author className={'symbol symbol-32 symbol-circle flex-shrink-0 me-2'} imageSize={26} symbolClass={'symbol-label fs-8'}
                            name={selectedAccount?.accountName ?? ""} profileUrl={selectedAccount.profilePicture} networkId={networkId} />
                        <div className="overflow-hidden" style={{ maxWidth: 130 }}>
                            <p className="fw-medium mb-0 text-ellipsis fs-8 text-black">{selectedAccount?.accountName ?? ""}</p>
                        </div>
                    </div>
                    {(isBtntext == "Reconnect" || isBtntext == "Connect") ?
                        <OverlayTrigger overlay={<Tooltip>{formatTemplateText(appContent?.tooltip_disconnect ?? "", { company: COMPANY_NAME })}</Tooltip>}>
                            <span className='me-1 fs-5 text-danger h-14px w-14px'>
                                <FontAwesomeIcon icon={'circle-info'} />
                            </span>
                        </OverlayTrigger>
                        : null}
                </Dropdown.Toggle>

                <Dropdown.Menu className="w-100">
                    {accountList.map(account => {
                        const { id, profilePicture, accountName } = account;
                        return <Dropdown.Item key={id} onClick={() => { if(isAccessible) dispatch(ACTION_UPDATE_SOCIAL_ACCOUNT(id, feed.id)) } }>
                            <div className="d-flex align-items-center">
                                <Author className={'symbol symbol-26 symbol-circle flex-shrink-0 me-2'}
                                    imageSize={26}
                                    symbolClass={'symbol-label fs-8'}
                                    name={accountName} profileUrl={profilePicture} networkId={networkId} />
                                <div className="overflow-hidden" style={{ maxWidth: 130 }}>
                                    <p className="fw-medium mb-0 text-ellipsis fs-9">{accountName}</p>
                                </div>
                            </div>
                        </Dropdown.Item>
                    })}
                </Dropdown.Menu>
            </Dropdown>
            : (isBtntext == "Reconnect" || isBtntext == "Connect") ? null : '--'}
        <div className="d-flex ms-3">
            {(isBtntext == "Reconnect" || isBtntext == "Connect") ?
                <OverlayTrigger placement="top" overlay={<Tooltip>{isBtntext}</Tooltip>}>
                    <Button variant='icon' className='btn-active-primary text-danger px-2 w-100' aria-label={isBtntext} onClick={() => connectAccount(feed, true)}>
                        <FontAwesomeIcon iconClass="me-1" type={'solid'} icon={'rotate'} />{isBtntext}
                    </Button>
                </OverlayTrigger>
                : null}
        </div>
    </div>
}

